.item-details {
  display: flex;
  flex-direction: row;
  padding: 1rem;
}

.card {
  background: #222;
}

.item-details h4 {
  font-size: 1.5rem;
}

.item-details .item-image {
  width: 30%;
  height: 30%;
  align-items: center;
  border-radius: 10px;
}

.item-details .list-group-item {
  padding: 0.25rem;
}

.item-details .list-group-item .term {
  margin-right: 0.5rem;
}
